
.sidebar{
  display: -ms-flexbox;
  display: flex;
  border: 1px solid #aaa;
  border-radius: 5px;
  min-height: 300px;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: #fff;
  min-width: 100px;
}
.sidebar > div{
  padding: 20px;
  transition: height .5s ease-in;
}
.sidebar > div ~ div{
  border-top: 1px dashed #aaa;
}
.sidebar > div .sidebar-btn{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  height: 50px;
  border-radius: 5px;
  background-color: #09c;
  color: #fff;
  cursor: pointer;
}
.sidebar > div .sidebar-btn input{
  display: none;
}
.sidebar .sidebar-infor{
  line-height: 20px;
}
.sidebar .sidebar-infor > div + div{
  margin-top: 10px;
}
.sidebar .settings .settings-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -ms-flex-align: center;
  align-items: center;
}
.sidebar .settings .settings-item + .settings-item{
  margin-top: 20px;
}
.sidebar .settings .settings-item input {
  display: block;
  padding: 5px 10px;
  border: 1px solid #aaa;
  background-color: #e9e9e9;
  border-radius: 4px;
}
.sidebar .settings .tip{
  margin-top: 10px;
}



